.grid-container {
  padding-top: 10px;
  display: grid;
  align-items: end;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;

  .grid-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      img {
          // width: 50px;
          // height: 50px;
          object-fit: cover;
      }

      .image-name {
          text-align: center;
          font-size: 12px;
          color: white;
          height: 20px;  // This sets a consistent height for the text area
          overflow: hidden;  // This ensures that any text that overflows this height is hidden
          white-space: nowrap;  // This ensures text doesn't wrap to the next line
          text-overflow: ellipsis;  // If text is too long, it'll end in '...'
      }
  }
}


.icon-container {
  position: absolute;
  transform: translate(0, -60%);
  width: 600px;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.icons-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.icon-row {
  display: flex;
  justify-content: center;
  margin: 15px 0;
  flex-wrap: wrap; // In case you have more icons and they don't fit in one row
  
  &:nth-child(3), &:nth-child(4) {
    justify-content: space-between;
  }
}

.icon {
  margin: 0 10px;
  width: 60px;
  height: 60px;
}

.icon-row-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.row-title {
  font-size: 16px;
  font-weight: bold;
  color: white;
  margin-bottom: 10px;
}

.icon-row {
  display: flex;
  justify-content: center;
  margin: 15px 0;
  flex-wrap: wrap;
  
  &:nth-child(3), &:nth-child(4) {
    justify-content: space-between;
  }
}

.icon-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
}

.icon {
  width: 60px;
  height: 60px;
}

.icon-name {
