}

.settings-window {
  position: absolute;
  top: 100%; // right below the image
  left: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.30) 0.53%, rgba(212, 212, 212, 0.27) 100%);
  border-radius: 20px;
  padding: 10px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  width: 200px; // or adjust to your preference
  z-index: 1;
  font-size: 12px;
  div {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}


